.outer-box{
    height: 100vh;
    width: 100vw;
    position: absolute;
    left: 0;
    background-color:white;
    border-radius:0% 0% 100% 0%;
}
.video-container{
    height: 85vh;
    width: 95vw;
    position: relative;
    margin: auto;
}
.my-video-container{
    position: inherit;
    left: 74%;
    top:7%;
    transition: left 1s;
}
.my-video{
    position: relative;
    z-index: 1;
    width: 25%;
    height: 25%;
    border: 0.1px solid black;
    transform: rotateY(180deg);
}
.my-name {
    padding: 5px;
    background-color: black;
    color: white;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
}
.partner-video-container{
    position: inherit;
    background-color: black;
    bottom: 25%;
    height: 75vh;
    max-width: 65vw;
}
.caller-video{
    display: block;
    height: 75vh;
    max-width: 65vw;
    margin: auto;
    z-index: 0;
    object-fit: cover;
}
.video-grid{
    display: grid;
    width: 100%;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    border: 2px solid blue;
}



.control-panel{
    width: 350px;
    align-items: center;
    height: 70px;
    padding: 10px;
    position: absolute;
    bottom: 0;
    left: 35%;
    display: grid;
    grid-template-columns: auto auto auto auto auto auto;
    column-gap: 15px;
    align-items: center;
}

.screen-share{
    border: 1px solid white;
    background: transparent;
    color:white;
    padding: 10px;
    font-size: 15px;
    width: 130px;
}

.screen-share:hover{
    background-color: white;
    color: purple;
}

.message-toggle{
    display: flex;
    width: auto;
    background: none;
    border: none;
    padding: 10px;
    color: black;
    outline: none;
}

.message-toggle:active{
    outline: none;
}

.control-button{
    border: none;
    background: none;
    display: flex;
    width: 50px;
    margin: auto;
    outline: none;

}

.hang-up-btn{
    width: 50px;
    height: 50px;
    outline: none;
    background-color: red;
    border-radius: 50% 50% 0 0;
    cursor: pointer;
}

.mic-icon{
    color: black;
    padding: 10px;
    height: 40px;
    width: 40px;
    transition: all 1s ease-out;
    border-radius: 50% 50% 0% 50%;
    margin: auto;
}

.cam-icon{
    color: black;
    padding: 10px;
    height: 40px;
    width: 40px;
    border-radius: 50% 50% 50% 0%;
    margin: auto;

}

.msg-icon{
    height: 50px;
    width: 50px;
    padding: 10px;
    border-radius: 50px;
}

.copy-to-clipboard{
    display: grid;
    grid-template-columns: auto auto;
    text-align: center;
    padding: 10px;
}

.copy-alert{
    background-color: plum;
    color: white;
    padding: 15px;
    position: absolute;
    left:-90vw;
    bottom: 9vh;
    font-weight: 500;
    transition: left 1s;
    z-index: 2;
}

.share-code-button{
    border:none;
    margin: auto;
    background-color: transparent;
    height: auto;
    width: 50px;
    padding: 2px;
    border-radius: 5px;
}
.share-code-button:hover{
    background-color: purple;
}

.caller-name{
    position: absolute;
    top: 0;
    left:0;
    z-index: 1;
    padding: 20px;
    background-color: black;
    color: white;
}
.join-alert{
    z-index: 1;
    padding: 10px;
    background-color: seagreen;
    width: auto;
    height: auto;
    position: absolute;
    font-size: 15px;
    margin: auto;
    bottom: 10vh;
    left: -50vw;
    align-items: center;
    transition: left 1s;
}

.join-name{
    padding: 14px;
    font-size: 17px;
    font-weight: 500;
    line-height: 10px;
    overflow-wrap: break-word;
    color: white;
}

.alt-image-vid-off{
    z-index: 1;
    background-color: black;
    margin: auto;
    width: 65vw;
    height: 40px;
    align-items: center;
    justify-content: center;
}
.alt-image-off{
    position: absolute;
    left: 29vw;
    top: 29vh;
    border: block 4px white;
    z-index: 0;
}

.default-message{
    position: absolute;
    left: 15vw;
    top:30vh;
    font-size: 60px;
}

.chat-window{
    height: 73vh;
    width: 25vw;
    background-color: black;
    position: absolute;
    right: 0;
    top: 1%;
    color: white;
    font-size: small;
    overflow-x: hidden;
    overflow-y: auto;
    padding: 10px;
}

.msg{
    overflow-wrap:break-word;
    font-weight: 500;
}

.msg-box{
    color: white;
    position: absolute;
    top: 73vh;
    right: 0;
    width: 25vw;
}

.msg-container{
    position: fixed;
    right: -60vw;
    top: 1vh;
    transition: right 1s;
}
.messageCard{
    padding: 8px;
    font-size: 15px;

}
.senderDetails{
    font-weight: 600;
    line-height: 0.1px;
}

.senderDetailsSelf{
    color: rgb(9, 243, 173);
}

.senderDetailsOther{
    color:rgb(14, 212, 247);
}

.message-send-button{
    background: white;
    border: none;
}



.button-details{
    font-weight: 600;
    display: block;
    margin: auto;
    color: black;
}



::-webkit-scrollbar{
    width: 10px;
}

::-webkit-scrollbar-track{
    background: darkgray;
}

::-webkit-scrollbar-thumb{
    background: grey;
}

::-webkit-scrollbar-thumb:hover{
    background: whitesmoke;
}